<template>
	<div class="seller" ref="seller">
		<div class="seller-content">
			<div class="overview">
				<h1 class="title">{{seller.name}}</h1>
				<div class="desc">
					<star :size="36" :score="seller.score">{{seller.score}}</star>
					<span class="text">({{seller.ratingCount}})</span>
					<span class="text">月售{{seller.sellCount}}单</span>
				</div>
				<ul class="remark">
					<li class="block">
						<h2>起送价</h2>
						<div class="content">
							<span class="stress">{{seller.minPrice}}</span>元
						</div> 
					</li>

					<li class="block">
						<h2>商家配送</h2>
						<div class="content">
							<span class="stress">{{seller.deliveryPrice}}</span>元
						</div> 
					</li>

					<li class="block">
						<h2>平均配送时间</h2>
						<div class="content">
							<span class="stress">{{seller.deliveryTime}}</span>分钟
						</div> 
					</li>
				</ul>
				<div class="favorite" @click="toggleFavorite">
					<span class="icon-favorite" :class="{'active':favorite}"></span>
					<span class="text">{{favoriteText}}</span>
				</div>
			</div>
			<split></split>
			<div class="bulletin">
				<h1 class="title">公告与活动</h1>
				<div class="content-wrapper">
					<p class="content">{{seller.bulletin}}</p>
				</div>
				<ul v-if="seller.supports" class="supports">
					<li class="support-item" v-for="(item,index) in seller.supports" :key="index">
						<span class="icon" :class="classMap[seller.supports[index].type]"></span>
						<span class="text">{{seller.supports[index].description}}</span>
					</li>
				</ul>
			</div>
			<split></split>
			<div class="pics">
				<h1 class="title">商家实景</h1>
				<div class="pic-wrapper" ref="picwrapper">
					<ul class="pic-list" ref="piclist">
						<li class="pic-item" v-for="(pic,index) in seller.pics" :key="index">
							<img :src="pic" width="120" height="90">
						</li>
					</ul>
				</div>
			</div>
			<split></split>
			<div class="info">
				<h1 class="title">商家信息</h1>
				<ul>
					<li class="info-item" v-for="(info,index) in seller.infos" :key="index">{{info}}</li>
				</ul>
			</div>
		</div>
	</div>
</template>

<script>
import BScroll from 'better-scroll'
import star from '../star/star.vue'
import split from '../split/split.vue'

	export default{
		props: {
			seller: {
				type: Object
			}
		},
		data() {
			return {
				favorite: true
			}
		},
		computed: {
			favoriteText(){
				return this.favorite ? '已收藏' : '收藏'
			}
		},
		created() {
			this.classMap = ['decrease','discount','special','invoice','guarantee']
		},
		methods: {
			toggleFavorite(event){
				if(!event._constructed){
					return
				} 
				this.favorite = !this.favorite
			}
		},
		mounted () {
			this.$nextTick(function () {
				this.scroll = new BScroll(this.$refs.seller,{
					click: true
				})
			})
			if(this.seller.pics){
				let picWidth = 120
				let margin = 6
				let width = (picWidth + margin) * this.seller.pics.length - margin
				this.$refs.piclist.style.width = width + 'px'
				this.$nextTick(() => {
					this.scroll = new BScroll(this.$refs.picwrapper,{
						scrollX: true,
						eventPassthrough: 'vertical'
					})
				})
			}
		},
		components: {
			star,
			split
		}
	}
</script>

<style lang="stylus">
	.seller
		position: absolute 
		top: 174px
		bottom: 0
		left: 0
		width: 100%
		overflow: hidden
		.overview
			position: relative
			padding: 18px
			.title
				margin-bottom: 8px
				line-height: 14px
				color: rgb(7,17,27)
				font-size: 14px
			.desc
				padding-bottom: 9px
				line-height: 18px
				border-bottom: 1px solid rgba(7,17,27,0.1)
				font-size: 0
				.star
					display: inline-block
					margin-right: 8px
					vertical-align: top
				.text
					margin-right: 12px
					display: inline-block
					vertical-align: top
					font-size: 10px
					color: rgb(77,85,93)
			.remark
				display: flex
				padding-top: 18px
				list-style: none
				.block
					flex: 1
					text-align: center
					border-right: 1px solid rgba(7,17,27,0.1)
					&:last-child
						border: none 
					h2
						margin-bottom: 4px
						line-height: 10px
						font-size: 10px
						color: rgb(147,153,159)
					.content
						line-height: 24px
						font-size: 10px
						color: rgb(7,17,27)
						.stress
							font-size: 24px
			.favorite
				position: absolute
				width: 40px
				right: 18px
				top: 12px
				text-align: center
				.icon-favorite
					display: block
					background-image: url(favorite1.png)
					width: 100%
					height: 25px
					background-size: 25px 25px
					background-repeat: no-repeat
					background-position: center
					// color: #d4d6d9
					// line-height: 24px
					//font-size: 24px
					// margin-bottom: 4px
					&.active
						background-image: url(favorite2.png)
				.text
					line-height: 10px
					font-size: 10px
					color: rgb(77,85,93)
		.bulletin
			padding: 18px 18px 
			.title
				margin-bottom: 8px
				line-height: 14px
				font-size: 14px
				color: rgb(7,17,27)
			.content-wrapper
				padding: 0 12px 16px 12px 
				border-bottom: 1px solid rgba(7,17,27,0.1)
				.content	
					line-height: 24px
					font-size: 12px
					color: rgb(240,20,20)
			.supports
				.support-item
					padding: 16px 12px
					border-bottom: 1px solid rgba(7,17,27,0.1)
					font-size: 0
					&:last-child
						border: none
					.icon
						display: inline-block
						vertical-align: top
						width: 16px
						height: 16px
						margin-right: 6px
						background-size: 16px 16px
						background-repeat: no-repeat
						&.decrease
							background-image: url(decrease_4@2x.png)
						&.discount
							background-image: url(discount_4@2x.png)
						&.guarantee
							background-image: url(guarantee_4@2x.png)
						&.invoice
							background-image: url(invoice_4@2x.png)
						&.special
							background-image: url(special_4@2x.png)
					.text
						line-height: 16px
						font-size: 12px
						color: rgb(7,17,27)
		.pics
			padding: 18px
			.title
				margin-bottom: 12px	
				line-height: 14px
				color: rgb(7,17,27)
				font-size: 14px
			.pic-wrapper
				width: 100%
				overflow: hidden
				white-space: nowrap 
				.pic-list
					font-size: 0
					.pic-item
						display: inline-block
						margin-right: 6px
						width: 120px
						height: 90px
						&:last-child
							margin: 0
		.info
			padding: 18px 18px 0 18px
			.title
				padding-bottom: 12px
				line-height: 14px
				border-bottom: 1px solid rgba(7,17,27,0.1)
				color: rgb(7,17,27)
				font-size: 14px
			ul
				list-style: none 
				.info-item
					padding: 16px 12px
					line-height: 16px
					border-bottom: 1px solid rgba(7,17,27,0.1)
					font-size: 12px
					&:last-child
						border: none

</style>


