<template>
	<div class="ratings" ref="ratings">
		<div class="ratings-content">
			<div class="overview">
				<div class="overview-left">
					<h1 class="score">{{seller.score}}</h1>
					<div class="title">综合评分</div>
					<div class="rank">高于周边商家{{seller.rankRate}}</div>
				</div>
				<div class="overview-right">
					<div class="score-wrapper">
						<span class="title">服务态度</span>
						<star :size="36" :score="seller.serviceScore"></star>
						<span class="score">{{seller.serviceScore}}</span>
					</div>
					<div class="score-wrapper">
						<span class="title">商品评分</span>
						<star :size="36" :score="seller.foodScore"></star>
						<span class="score">{{seller.foodScore}}</span>
					</div>
					<div class="delivery-wrapper">
						<span class="title">送达时间</span>
						<span class="delivery">{{seller.deliveryTime}}分钟</span>
					</div>
				</div>
			</div>
			<split></split>
			<ratingselect :selectType="selectType" :onlyContent="onlyContent" :ratings="ratings" @ratingtypeselect="ratingtypeselect" @contenttoggle="contenttoggle"></ratingselect>
			<div class="rating-wrapper">
				<ul>
					<li class="rating-item" v-for="(rating,index) in ratings" :key="index" v-show="needShow(rating.rateType,rating.text)">
						<div class="avatar">
							<img width="24" height="24" :src="rating.avatar">
						</div>
						<div class="content">
							<h1 class="name">{{rating.username}}</h1>
							<div class="star-wrapper">
								<star :size="36" :score="rating.score"></star>
								<span class="delivery" v-show="rating.deliveryTime">{{rating.deliveryTime}}</span>
							</div>
							<p class="text">{{rating.text}}</p>
							<div class="recommend" v-show="rating.recommend && rating.recommend.length">
								<span class="icon-thumb_up"></span>
								<span class="item" v-for="(item,index) in rating.recommend" :key="index">{{item}}</span>
							</div>
							<div class="time">
								{{rating.rateTime}}
							</div>
						</div>
					</li>
				</ul>
			</div>
		</div>
	</div>
</template>

<script>
import BScroll from 'better-scroll'
import star from '../star/star.vue'
import split from '../split/split.vue'
import ratingselect from '../ratingSelect/ratingselect.vue'

const ALL = 2
const ERR_OK = 0
	export default{
		props: {
			seller: {
				type: Object
			}
		},
		 data(){
        	return {
            	ratings: [],
            	selectType: ALL,
				onlyContent: true
        	} 
		},
		created () {
			this.$http.get('/api/ratings').then((response) =>{
				response = response.body
				if(response.errno === ERR_OK){
					this.ratings = response.data
					this.$nextTick(() => {
						this.Scroll()
					})	
				}	
			})
		},
		methods: {
			Scroll(){
				this.scroll = new BScroll(this.$refs.ratings,{
					click: true
				})
			},
			needShow(type,text){ //这里的逻辑有问题
            	if(this.onlyContent && !text){
                	return false
            	}
            	if(this.selectType === ALL){
                	return true
            	}else{
                	return type === this.selectType
            	}
        	},
        	ratingtypeselect(type){ //子组件已传入事件，父组件需要写入的两个事件方法
            	this.selectType = type
            	this.$nextTick(() => {
                	this.scroll.refresh()
            	})
       		},
        	contenttoggle(onlyContent){
            	this.onlyContent = onlyContent
            	this.$nextTick(() => {
                	this.scroll.refresh()
            	})
        	}
		},
		components: {
			star,
			split,
			ratingselect
		}
	}
</script>

<style lang="stylus">
	.ratings	
		position: absolute 
		top: 174px
		bottom: 0
		left: 0
		width: 100%
		overflow: hidden
		.overview
			display: flex
			padding: 18px 0
			.overview-left
				flex: 0 0 137px
				width: 137px
				border-right: 1px solid rgba(7,17,27,0.1)
				text-align: center
				padding: 6px 0	
				.score
					margin-bottom: 6px
					line-height: 28px
					font-size: 24px
					color: rgb(255,153,0)
				.title
					margin-bottom: 8px
					line-height: 12px
					font-size: 12px
					color: rgb(7,17,27)
				.rank
					line-height: 10px
					font-size: 	10px
					color: rgb(147,153,159)
			.overview-right
				flex: 1
				padding: 6px 0 6px 24px			
				.score-wrapper
					margin-bottom: 8px
					font-size: 0
					.title
						line-height: 18px
						display: inline-block
						font-size: 12px
						color: rgb(7,17,27)
					.star
						line-height: 18px
						display: inline-block
						margin: 0 12px
						vertical-align: top
					.score
						line-height: 18px
						display: inline-block
						vertical-align: top
						font-size: 12px
						color: rgb(255,153,0)
				.delivery-wrapper
					font-size: 0
					.title
						line-height: 18px
						font-size: 12px
						color: rgb(7,17,27)
					.delivery
						margin-left: 12px
						font-size: 12px
						color: rgb(147,153,159)
		.rating-wrapper
			padding: 0 18px
			.rating-item
				display: flex	
				padding: 18px 0
				border-bottom: 1px solid rgba(7,17,27,0.1)
				.avatar
					flex: 0 0 28px
					width: 28px
					margin-right: 12px
					img 
						border-radius: 50%
				.content
					position: relative
					flex: 1
					.name
						margin-bottom: 4px
						line-height: 12px
						font-size: 10px
						color: rgb(7,17,27)
					.star-wrapper
						margin-bottom: 6px
						font-size: 0
						text-align: left
						//position: relative
						.star
							display: inline-block
							margin-right: 6px
							vertical-align: top
							// position: absolute
							// left: 0
							// bottom: 3px
						.delivery
							display: inline-block
							vertical-align: top
							font-size: 10px
							color: rgb(147,153,159)
					.text	
						margin-bottom: 8px
						line-height: 18px
						color: rgb(7,17,27)
						font-size: 12px
					.recommend
						line-height: 16px
						font-size: 0
						.item
							display: inline-block
							margin: 0 8px 4px 0
							font-size: 9px
							padding: 0 6px
							border: 1px solid rgba(7,17,27,0.1)
							border-radius: 1px
							color: rgb(147,153,159)
							background: #ffffff
					.time
						position: absolute 
						top: 0
						right: 0
						line-height: 12px
						font-size: 10px
						color: rgb(147,153,159)
	.icon-thumb_up{
		display: inline-block;
		background-image: url(ios-thumbs-up.png);
                            width: 12px;
                            height: 12px;
                           	background-size: 12px 12px;
                            background-repeat: no-repeat;
						}
							


							
</style>


